body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.axis line, .axis path {
    stroke-width: 2px;
    stroke: #007f8b;
}

.grid {
    stroke: #007f8b;
    stroke-width: 2;
}

.subGrid {
    stroke: #007f8b;
    stroke-width: 0.5;
}
.controlPanel{
    width: 200px;
}

.slider-control {
    margin: 15px;
}
.container{
  display: flex;
  flex-flow: row;
}

.control {
  flex: 1 1;
}

.canvas {
  flex:1 1
}
